let idleAnimationCounterDictionary = {};
export const addIdleCounterToRegistry = (id, counter) => {
    idleAnimationCounterDictionary[id] = counter;
};
export const removeIdleCounterFromRegistry = (id) => {
    delete idleAnimationCounterDictionary[id];
};
export const clearIdleCounterRegistry = () => {
    idleAnimationCounterDictionary = {};
};
export const getAllIdleCounters = () => {
    return idleAnimationCounterDictionary;
};
