import globals from '../../game-data/globals';
import { GRID_BLOCK_PX, GRID_BLOCK_IN_SHEET_PX } from '../../game-data/globals';
import { mobileAgent } from '../../helpers/screenOrientation';
export const drawBubblePart = (name, tile, ctx) => {
    const pngs = globals.PNG_DICTIONARY;
    ctx.drawImage(pngs[name], 0, 0, GRID_BLOCK_IN_SHEET_PX, GRID_BLOCK_IN_SHEET_PX, tile.x, tile.y, GRID_BLOCK_PX, GRID_BLOCK_PX);
};
export const getTabXPosition = (index, activeIndex) => {
    if (mobileAgent) {
        return 0;
    }
    return (index * (4 * GRID_BLOCK_PX)) + ((activeIndex < index) ? 8 * GRID_BLOCK_PX : 0 * GRID_BLOCK_PX);
};
