import { CollectableType } from "../enumerables/CollectableTypeEnum";
let coins = [];
let totalCoins = 100;
let juiceCans = [];
let totalJuiceCans = 100;
export const loadCollectableRegistry = (savedRegistry) => {
    coins = savedRegistry.coins;
    juiceCans = savedRegistry.juiceCans;
};
export const addCollectableToRegistry = (collectableId, type) => {
    switch (type) {
        case CollectableType.coin:
            coins.push(collectableId);
            break;
        case CollectableType.can:
            juiceCans.push(collectableId);
            break;
    }
};
export const isInCollectableRegistry = (collectableId, type) => {
    switch (type) {
        case CollectableType.coin:
            return coins.indexOf(collectableId) > -1;
        case CollectableType.can:
            return juiceCans.indexOf(collectableId) > -1;
    }
};
export const getCollectableId = (column, row, type, mapName) => {
    return `col_${column}_row_${row}_${mapName}_${type}`;
};
export const exportCollectableRegistry = () => {
    return {
        coins: coins,
        juiceCans: juiceCans
    };
};
export const setCollectableRegistry = (registryObject) => {
    coins = [...registryObject.coins];
    juiceCans = [...registryObject.juiceCans];
};
