import { getAllDoors } from "./doorRegistry";
export const getSpriteAssociatedDoor = (spriteId) => {
    const doors = getAllDoors();
    return doors[spriteId];
};
export const getTileAssociatedDoor = (tileIndex) => {
    const doors = getAllDoors();
    return doors[tileIndex.toString()];
};
export const getActiveDoors = () => {
    return getAllDoors();
};
