import { playMusic } from "../game/sound/sound";
import { drawRect } from "./canvasHelpers";
const RGB = "0, 0, 0, ";
let faderCanvas = null;
let fadingToBlack = false;
let fadingFromBlack = false;
let holdBlackScreen = false;
let fadeInAndOut = false;
let A = null;
export const getRGBA = () => { return "rgba( " + RGB + A + ")"; };
export const inFadingAnimation = () => { return fadingFromBlack || fadingToBlack || holdBlackScreen; };
export const fadedOut = () => { return !fadingToBlack && holdBlackScreen; };
export const setFaderCanvas = (width, height) => {
    faderCanvas = new OffscreenCanvas(width, height);
};
export const startFadeToBlack = (fadeBack = false) => {
    fadeInAndOut = fadeBack;
    fadingToBlack = true;
    A = 0;
};
export const startFadeFromBlack = () => {
    fadingFromBlack = true;
    A = 1;
};
export const handleFadeAnimation = () => {
    faderCanvas.getContext('2d').clearRect(0, 0, screen.width, screen.height);
    drawRect(faderCanvas, 0, 0, screen.width, screen.height, getRGBA());
    if (fadingFromBlack) {
        fadeFromBlack();
    }
    else if (fadingToBlack) {
        fadeToBlack();
    }
    checkForFadeEnd();
};
export const getFaderCanvas = () => {
    return faderCanvas;
};
const fadeToBlack = () => {
    A += .0250;
};
const fadeFromBlack = () => {
    A -= .0250;
};
const holdBlackMode = () => {
    holdBlackScreen = true;
};
const checkForFadeEnd = () => {
    if (fadingFromBlack && A <= 0) {
        unsetFadingAnimation();
        playMusic();
    }
    else if (fadingToBlack && A >= 1 && fadeInAndOut) {
        unsetFadingAnimation();
        startFadeFromBlack();
    }
    else if (fadingToBlack && A >= 1) {
        unsetFadingAnimation();
        holdBlackMode();
    }
};
const unsetFadingAnimation = () => {
    holdBlackScreen = false;
    fadingToBlack = false;
    fadingFromBlack = false;
    fadeInAndOut = false;
};
