import { GRID_BLOCK_PX } from "../../../game-data/globals";
import { ActionSelector } from "../../map/map-classes/ActionSelector";
import { addSpriteAction, addTileAction, clearActionRegistry, removeSpriteAction, removeTileAction } from "./actionRegistry";
export const initializeActionForSprite = (sprite, interactionList) => {
    const action = new ActionSelector(sprite.centerX, sprite.baseY, interactionList, sprite.spriteId);
    addSpriteAction(sprite.spriteId, action);
};
export const initializeActionForTile = (tile, interactionList) => {
    const action = new ActionSelector(tile.x + (GRID_BLOCK_PX / 2), tile.y + (GRID_BLOCK_PX / 2), interactionList);
    addTileAction(tile.index.toString(), action);
};
export const destroySpriteAssociatedAction = (spriteId) => {
    removeSpriteAction(spriteId);
};
export const destroyTileAssociatedAction = (tile) => {
    removeTileAction(tile.index.toString());
};
export const clearActions = () => {
    clearActionRegistry();
};
