let unlockedDoors = [];
export const inUnlockedDoorsRegistry = (doorId) => {
    return unlockedDoors.includes(doorId);
};
export const addDoorToUnlockedDoorsRegistry = (doorId) => {
    unlockedDoors.push(doorId);
};
export const getUnlockedDoorsRegistry = () => {
    return unlockedDoors;
};
export const setUnlockedDoorsRegistry = (doorList) => {
    unlockedDoors = doorList;
};
