let events = [];
let triggeredEventIds = [];
let storyEventsForMap = [];
export const addStoryEventForMapToRegistry = (event) => {
    storyEventsForMap.push(event);
};
export const getRegistryStoryEventsForMap = () => {
    return storyEventsForMap;
};
export const clearRegistryStoryEventsForMap = () => {
    storyEventsForMap = [];
};
export const getStoryEventRegistry = () => {
    return events;
};
export const getTriggeredStoryEventIds = () => {
    return triggeredEventIds;
};
export const addStoryEventToRegistry = (event) => {
    events.push(event);
};
export const markStoryEventAsTriggered = (eventId) => {
    triggeredEventIds.push(eventId);
};
