let paused;
let debugMode;
let disableStory;
let listeningForKeys;
export const setPausedGameState = (value) => {
    paused = value;
};
export const setDebugModeGameState = (value) => {
    debugMode = value;
};
export const setDisableStoryGameState = (value) => {
    disableStory = value;
};
export const setListeningForKeysGameState = (value) => {
    listeningForKeys = value;
};
export const getPausedGameState = () => {
    return paused;
};
export const getDebugModeGameState = () => {
    return debugMode;
};
export const getDisableStoryGameState = () => {
    return disableStory;
};
export const getListentingForKeysGameState = () => {
    return listeningForKeys;
};
