import { MAX_BUBBLE_TEXT_WIDTH } from '../game-data/globals';
import { getBackSpritesGrid } from '../game/canvas/canvasGetter';
export const drawFromImageToCanvas = (image, imageX, imageY, widthInImage, heightInImage, canvasX, canvasY, widthInCanvas, heightInCanvas) => {
    const ctx = getBackSpritesGrid().ctx;
    ctx.drawImage(image, imageX, imageY, widthInImage, heightInImage, canvasX, canvasY, widthInCanvas, heightInCanvas);
};
export const setFont = (size, ctx = getBackSpritesGrid().ctx) => {
    ctx.font = size + "px " + 'PFRondaSeven';
};
export const breakTextIntoLines = (text, fontSize) => {
    const ctx = getBackSpritesGrid().ctx;
    setFont(fontSize);
    if (ctx.measureText(text).width > MAX_BUBBLE_TEXT_WIDTH) {
        const textArray = text.split(' ');
        let currentLineWidth = 0;
        let textLine = "";
        const textLineArray = [];
        for (let i = 0; i < textArray.length; i++) {
            setFont(fontSize);
            const newWord = textArray[i] + " ";
            const wordOverflowsTextbox = currentLineWidth + ctx.measureText(newWord + " ").width > MAX_BUBBLE_TEXT_WIDTH;
            const lastWordIsNext = i === textArray.length - 1;
            if (wordOverflowsTextbox) {
                textLineArray.push(textLine);
                textLine = newWord;
                currentLineWidth = 0;
                if (lastWordIsNext) {
                    textLineArray.push(textLine);
                }
            }
            else {
                currentLineWidth += ctx.measureText(newWord).width;
                textLine += newWord;
                if (lastWordIsNext) {
                    textLineArray.push(textLine);
                }
            }
        }
        return textLineArray;
    }
    return [text];
};
export const drawRect = (canvas, x, y, width, height, color = null) => {
    const ctx = canvas.getContext('2d', { alpha: false });
    ctx.fillStyle = (color !== null) ? color : "white";
    ctx.fillRect(x, y, width, height);
};
export const writeTextLine = (text, x, y, size, ctx = getBackSpritesGrid().ctx, color = "black") => {
    setFont(size, ctx);
    ctx.fillStyle = color;
    ctx.fillText(text, x, y);
};
