import { InteractionAnswer } from "../enumerables/InteractionAnswer";
let interactionRegistry = {};
export const isInRegistry = (key) => {
    return key in interactionRegistry;
};
export const isInRegistryWithValue = (key, value) => {
    if (isInRegistry(key)) {
        return interactionRegistry[key] === value;
    }
    return false;
};
export const addEventToRegistry = (eventKey, value = InteractionAnswer.yes) => {
    interactionRegistry[eventKey] = value;
};
export const getRegistry = () => {
    return interactionRegistry;
};
export const setInteractionRegistry = (registryObject) => {
    interactionRegistry = registryObject;
};
