import { InteractionAnswer } from "../../../enumerables/InteractionAnswer";
import { Hitbox } from "../../core/Hitbox";
import { GRID_BLOCK_PX } from "../../../game-data/globals";
import { conditionIsTrue } from "../../../helpers/conditionalHelper";
import { InteractionType } from "../../../enumerables/InteractionType";
import { CinematicTrigger } from "../../../enumerables/CinematicTriggerEnum";
import { addCollectableToRegistry, getCollectableId } from "../../../registries/collectableRegistry";
import { getActiveMapKey } from "../../neighbourhoodModule";
import { setActiveCinematic } from "../../controllers/cinematicController";
export class ActionSelector extends Hitbox {
    constructor(x, y, actionList, spriteId = null) {
        super(x, y, GRID_BLOCK_PX / 2);
        this.spriteId = spriteId;
        this.conditionalActions = actionList;
        this.trigger = CinematicTrigger.interaction;
        this.arcColor = "#FF0000";
        this.registeredSelection = null;
        this.confirmingAction = false;
        this.wasMoving = false;
        this.checkForConditions();
    }
    get meetsCondition() { return conditionIsTrue(this.activeAction.condition.type, this.activeAction.condition.value); }
    get needsConfirmation() { return this.activeAction.type != InteractionType.talk; }
    updateXy(x, y) {
        this.checkForConditions();
        super.updateXy(x, y);
    }
    checkForConditions() {
        for (var i = 0; i < this.conditionalActions.length; i++) {
            let currentAction = this.conditionalActions[i];
            if (conditionIsTrue(currentAction.condition.type, currentAction.condition.value)) {
                this.activeAction = currentAction;
                return;
            }
        }
    }
    handle(sprite) {
        console.log('handle');
        setActiveCinematic(this.activeAction, this.trigger, [this.spriteId]);
        if (sprite.model.isCollectable) {
            const id = getCollectableId(sprite.column, sprite.row, sprite.collectableType, getActiveMapKey());
            addCollectableToRegistry(id, sprite.collectableType);
        }
    }
    confirm() {
        console.log('confirm');
        this.confirmingAction = true;
    }
    dismiss() {
        console.log('dismiss');
        if (this.needsConfirmation && this.registeredSelection == InteractionAnswer.yes && !this.confirmingAction) {
            this.confirm();
        }
        else {
            this.resetAction();
        }
    }
    registerSelection(selection) {
        this.registeredSelection = selection;
    }
    resetAction() {
        this.confirmingAction = false;
        this.checkForConditions();
    }
}
