let doorDictionary = {};
export const addDoorToRegistry = (key, door) => {
    doorDictionary[key] = door;
};
export const removeDoorFromRegistry = (key) => {
    delete doorDictionary[key];
};
export const getAllDoors = () => {
    return doorDictionary;
};
export const clearDoorRegistry = () => {
    doorDictionary = {};
};
