import { CanvasTypeEnum } from "../../enumerables/CanvasTypeEnum";
import { getCanvasFromRegistry, getDOMCanvasFromRegistry, getDOMContextFromRegistry, getPreRenderCanvasFromRegistry, getPreRenderContextFromRegistry } from "./canvasRegistry";
// CanvasGrid class extensions
export const getBackTilesGrid = () => {
    return getCanvasFromRegistry(CanvasTypeEnum.background);
};
export const getBackSpritesGrid = () => {
    return getCanvasFromRegistry(CanvasTypeEnum.backSprites);
};
export const getFrontTilesGrid = () => {
    return getCanvasFromRegistry(CanvasTypeEnum.foreground);
};
export const getMenuGrid = () => {
    return getCanvasFromRegistry(CanvasTypeEnum.overview);
};
export const getSpeechBubbleGrid = () => {
    return getCanvasFromRegistry(CanvasTypeEnum.speechBubbleCanvas);
};
// rendering canvas elements
export const getPreRenderCanvas = () => {
    return getPreRenderCanvasFromRegistry();
};
export const getPreRenderContext = () => {
    return getPreRenderContextFromRegistry();
};
export const getDOMCanvas = () => {
    return getDOMCanvasFromRegistry();
};
export const getDOMContext = () => {
    return getDOMContextFromRegistry();
};
// gridcanvas tile getter
export const getTileOnCanvasByIndex = (index, canvasType) => {
    const canvas = getCanvasFromRegistry(canvasType);
    return canvas.getTileAtIndex(index);
};
export const getTileOnCanvasByCell = (cell, canvasType) => {
    const canvas = getCanvasFromRegistry(canvasType);
    return canvas.getTileAtCell(cell.column, cell.row);
};
export const getTileOnCanvasByXy = (xy, canvasType) => {
    const canvas = getCanvasFromRegistry(canvasType);
    return canvas.getTileAtXY(xy.x, xy.y);
};
