let randomAnimationCounterDictionary = {};
export const addRandomCounterToRegistry = (id, counter) => {
    randomAnimationCounterDictionary[id] = counter;
};
export const removeRandomCounterFromRegistry = (id) => {
    delete randomAnimationCounterDictionary[id];
};
export const clearRandomCounterRegistry = () => {
    randomAnimationCounterDictionary = {};
};
export const getAllRandomCounters = () => {
    return randomAnimationCounterDictionary;
};
