let hitboxDictionary = {};
export const addHitboxToRegistry = (id, hitbox) => {
    hitboxDictionary[id] = hitbox;
};
export const removeHitboxFromRegistry = (id) => {
    delete hitboxDictionary[id];
};
export const getActiveHitboxes = () => {
    return hitboxDictionary;
};
export const clearHitboxRegistry = () => {
    hitboxDictionary = {};
};
