import { getAssociatedIdleSpriteCounter } from "./idleAnimGetter";
export const incrementIdleAnimationCounter = (spriteId) => {
    const counter = getAssociatedIdleSpriteCounter(spriteId);
    counter.count();
};
export const idleAnimationCounterIsOverLimit = (spriteId) => {
    const counter = getAssociatedIdleSpriteCounter(spriteId);
    return counter.isCounterOverLimit();
};
export const getIdleAnimationFromList = (sprite) => {
    const animationList = sprite.model.idleAnimations;
    return animationList[Math.floor(Math.random() * animationList.length)];
};
export const resetIdleAnimationCounter = (spriteId) => {
    const counter = getAssociatedIdleSpriteCounter(spriteId);
    counter.resetCounter();
};
