let blockedSpriteCounterDictionary = {};
export const addBlockedCounterToRegistry = (id, counter) => {
    blockedSpriteCounterDictionary[id] = counter;
};
export const removeBlockedCounterFromRegistry = (id) => {
    delete blockedSpriteCounterDictionary[id];
};
export const clearBlockedCounterRegistry = () => {
    blockedSpriteCounterDictionary = {};
};
export const getAllBlockedCounters = () => {
    return blockedSpriteCounterDictionary;
};
