let animationDictionary = {};
export const addAnimationToRegistry = (id, animation) => {
    animationDictionary[id] = animation;
};
export const deleteAnimationFromRegistry = (id) => {
    delete animationDictionary[id];
};
export const getAnimationRegistry = () => {
    return animationDictionary;
};
export const clearAnimationRegistry = () => {
    animationDictionary = {};
};
